@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
@import "../../styles/variables.css";

.notfoundSection {
  background: linear-gradient(180deg, #ffffff 0%, #ffffff 25%, #bedbb0 92.19%);
  position: relative;
  height: 100%;
}

.notfoundSection .notfound {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  height: -webkit-fill-available;
}

.notfound {
  /* display: flex;
  align-items: center;
  justify-content: center; */
  max-width: 410px;
  width: 100%;
  text-align: center;
  height: -webkit-fill-available;
  background: linear-gradient(180deg, #ffffff 0%, #ffffff 25%, #bedbb0 92.19%);
}

.notfound .notfound-404 {
  /* display: flex;
  align-items: center;
  justify-content: center; */
  height: 280px;
  position: relative;
  z-index: -1;
}

.notfound .notfound-404 h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 250px;
  margin: 0px;
  font-weight: 900;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  background: #b8bcfd;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: cover;
  background-position: center;
  z-index: 100000000;
}

.notfound h2 {
  font-family: "Montserrat", sans-serif;
  color: #000;
  font-size: 27px;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 35px;
  margin-bottom: 20px;
}

.notfound p {
  font-family: "Montserrat", sans-serif;
  color: #000;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 40px;
  margin-top: 0px;
  font-style: italic;
}

.notfound a {
  font-family: "Montserrat", sans-serif;
  font-size: 14px;
  text-decoration: none;
  text-transform: uppercase;
  background: #94c01a;
  display: inline-block;
  padding: 15px 30px;
  border-radius: 8px;
  color: #fff;
  font-weight: 700;
  -webkit-box-shadow: 0px 4px 15px -5px #c7dbb0;
  box-shadow: 0px 4px 15px -5px #b7dbb0;
  transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out,
    color 0.2s ease-in-out;
}

.notfound a:hover {
  opacity: 0.9;
  transform: scale(1.05);
}

.notfound a:active {
  opacity: 0.8;
  transform: scale(1.1);
  color: black;
}

@media only screen and (max-width: 767px) {
  /* .notfound .notfound-404 {
    height: 142px;
  } */

  .notfound .notfound-404 h1 {
    font-size: 112px;
    margin-top: 100px;
  }

  .notfound h2 {
    font-size: 20px;
  }
}

@media only screen and (min-width: 767px) {
  .notfoundSection {
    background: transparent;
  }

  .notfound .notfound-404 h1 {
    font-size: 230px;
  }

  .notfound .notfound-404 {
    height: 250px;
  }
}
